import React, { Component } from 'react';





class DYH extends Component {
    render() {


        return (
            <div>
                <h2>巴黎最后的探戈</h2>
                <p>时常能想起这部电影中急促却又充满着遥远乡愁的爵士音乐，那些仿佛自原始蛮荒的场景中传来的鼓点，夹杂着岁月流逝的沙沙声，与时而情绪激动歇斯底里，时而平滑如水行走如风的萨克斯一起在这部极为“贝托鲁奇式的”影片中带出迷人的色彩影像，魅惑而不动声色的镜头运动，男女主人公疯狂爱欲……，这些让人眼花缭乱，困惑撩人的元素，使这部电影乍看上去就像是一个谜团。</p>
                <p>的确有许多谜难以解释，除了隐约的猜测和直觉的感受之外，我们只有满腹狐疑了，从影片一开始，我们就看见了一个因为妻子死亡而痛苦万分的中年美国男人—保罗，在大街上哭泣，他的头顶是一辆呼啸而过的天桥轨道电车，此时年轻的让娜从后面走过来……随后的场景中，一个女佣一边用心擦洗着保罗妻子的自杀现场中的血迹，一边喃喃自语保罗一生的漂泊经历，而保罗就在一墙之隔的地方沉默地听她的低语，在她挑衅般地把那把自杀用的剃刀递给保罗时，保罗一把把女佣摁在了墙上，那姿势好像是要杀了她一般……公寓的管理员，那个黑女人怪异的笑声……保罗的岳母闻讯赶来，他们两人之间的谈话更为让人困惑，镜头在他们之间来回摇动，似乎在暗示他们之间有什么秘密般，包括保罗粗暴的态度，岳母过分地低声下气却又疑虑满腹……，还有，保罗的妻子为什么要自杀？年轻的巴黎姑娘让娜为什么一次又一次来到保罗粗暴充满发泄性质的怀抱里？片头的美国画家—弗朗西斯培根的那些油画又暗示什么？这些众多的谜一样的表情场景与难以解释的感情，混乱，苍凉，欲言又止，一如漂泊着的爱欲，发生在这个漂浮中色彩恍惚的电影巴黎之内。</p>


                <p>在很多次地观看这部电影后，我逐渐看见了几个清晰的空间，但是，这几个空间里发生的事情仍有些晦暗不明。主要的空间就是让娜与保罗相遇的那个公寓，保罗此时只是想逃避那个无可逃避的情感深渊，逃避妻子的死亡而带来的情感断裂，与这种断裂带来的巨大空洞，身体上的，情感上的。而让娜，大概只是为了男友从外地到来而想租个临时住所而已，她在公寓遇到这个神情憔悴哀伤的美国人，由于他不肯透露姓名，由于他的表情与粗壮的体魄，神秘感油然而生，然而更重要的是，这个偶然而充满随机性的场景提供了事情发生的氛围，这间待租的，空荡却又充满莫名回忆的巴黎公寓，隐藏在都市冷漠充满秩序的表情之下，就像一个洞穴，爱欲和逃避之洞，而让娜和保罗就在这个偶然的秘密洞穴中里会面了，就好像冥冥中先有了一个神秘安排般，所以突然响起的电话铃声使他们分别在两个房间里用分机接了，但是电话那头只是传来彼此的声音，让娜如梦魇般拿着听筒听着保罗的呼吸声，而保罗正悄悄放下听筒，向她走来。</p>

                <p> 我想，在这个空间里，两人的情感指向还是清晰的，保罗分明是为了逃避，他想把自己埋葬在无尽而疯狂的欲望发泄里，而且，他只要偶然，所以他绝不要姓名，绝不要偶然爱欲以外的东西，而让娜，就是让保罗所带来的这种神秘感迷住了，在这个只有一张做爱用的大床的公寓里，她曾无数次感到害怕，欲望满足后的空虚，与保罗的态度所带来的羞怒，但是她仍不由自主回来到这里，乖乖地脱衣，这种极端矛盾着的东西一如欲望本身，令人害怕又令人着魔。那个后来令导演贝托鲁奇声名狼藉的肛交场面的确令人瞠目，这些私密极端的东西令这个公寓更加压抑，也让我们对保罗表面强硬粗暴其实内心脆弱孤独的状态有所了解，而让娜，她的报复手段，就是一个小恶作剧：她骗保罗帮她摆弄唱片机，保罗被插头漏出的电狠狠击了一下。</p>
                <p>保罗与让娜的一场对话很重要，在让娜穿着婚纱冒着大雨来公寓找保罗后，保罗像对待小女儿般给她洗澡，让娜说她找到了一个神秘，高大，强壮的爱人，保罗说：</p>
                <p>你需要有人照顾你，保护你？</p>
                <p>是的。</p>
                <p>你需要伟大的战士，建立堡垒让你躲进去，令你不觉得害怕，寂寞，空虚。</p>
                <p>是的。</p>
                <p>……你找不到这样的人，你永远也找不到。</p>
                <p>不，我找到了。</p>
                <p>不久后，他就需要你帮他建立堡垒，从你的胸部，微笑，毛发和味道建立起来，一个让他觉得安全和舒适的地方。</p>
                <p>但是我找到了。</p>
                <p>不，你是孤单的，你永远孤单，你无法逃脱寂寞的感觉，直到你死去……</p>

                <p>仔细聆听！从这段对话里，我们可以窥见保罗内心的秘密，和由此带来的另一空间，就是保罗和妻子经营的小旅馆（我们也该模糊地想到了保罗妻子的死因），我猜，那个在巴黎的旅馆就像是一个寓言，保罗曾经在妻子的尸体前说：“昨晚我关灯，你所有的客人都跑出来了，我也是这些客人中的一个，对吗？”这个客人--主人川流不息的场所，是保罗的家，也是都市中的某个旅馆，在那些窗户上的毛玻璃背后，有来自世界各地的，漂泊的旅人，而保罗也是这些旅人中的一个，只不过一个偶然的机会留了下来，但是他永远也没能了解他的妻子，那似乎也是不可能了解的，他连她究竟为什么自杀都不知道。这种彻底的疏离感，多次透过保罗的双眼，映照在旅馆这个空间的每个角落之内。</p>
                <p>影片处处流露出都市中那种无处不在的漂流感，在这个经历复杂的美国人身上，在旅馆这个空间之内，在街上的各种肤色的行人之中。</p>

                <p>让娜和未婚夫汤姆所处的又是一个空间，大多是在室外，相比较而言，比较正常，就连汤姆某些时候表现的神经质，也是这个年龄段“文艺青年”的正常做派，那是一种略带表演性质的，害怕被人注意又生怕别人不注意的心理所导致的行为和说话方式，这种一本正经却又带有神经质的劲头使汤姆和让娜之间的感情不易深入，而随汤姆而来的拍摄小组，就好像一组无处不在的窥视的眼睛（这却是使汤姆激动的原因之一，而他自己恰恰是这场窥视下的爱情戏充满激情的拙劣导演），紧紧盯着这对年轻人，看着他们在室外表演爱情，在汤姆的“引导”下，让娜带着摄制小组在乡下的老屋拍摄，她在老屋中讲述的童年回忆也充满了表演性，（这与下一场戏中，让娜和保罗在公寓中的对话形成对比，与汤姆相比，保罗更乐意刺入事物的内部，他直逼让娜更为隐秘的回忆—初次的性萌动和与之有关的成长经验）</p>
                <p>让娜与汤姆的关系暗指了一个充满秩序但却冰冷无趣的现实世界，当让娜想要飞翔时，汤姆却要她“像个大人一般严肃”，所以让娜一次次从汤姆那里逃出，来到保罗的爱欲洞穴，在地铁的一场，当让娜第一次想冲破这现实之网大声对对面的汤姆说“我要离开你”时，飞驰而过的巨大钢铁机器—地铁使让娜的表白只剩下了无声的叫喊，后来汤姆突然出现在气急败坏的让娜面前，两人像个拳击手般相互打斗，这些观念性的场面都是使影片成为充满魅惑的谜语的原因。</p>

                <p>“拳击”或“探戈”就像是一个事物的两面，用来暗喻爱情，的确能反映出两性之间相互折磨，相互争斗又相互依存，相互伴随的关系，激烈如拳击，仪式化如探戈，在这些观念性的语汇中，爱情难道必然有悲惨的结局？在影片中让娜和汤姆准备结婚，在婚纱店试衣，周围是忙碌的店员，还有始终是如影随形的拍摄小组，让娜和汤姆“通俗的婚礼”在众人面前通俗的预演，汤姆激动地喃喃自语，夸赞未婚妻的美丽，爱情的纯洁，他甚至走出门去，夸张地站在雨中诉说自己内心的激情，他陶醉在自己的激情里，而让娜，早已跑到了保罗那里，告诉保罗她对他的迷恋和爱。</p>
                <p>保罗坚持说一切刚刚开始，两人在一个探戈舞场谈话，保罗告诉了让娜自己的身份，神秘感被消除，“一个旅馆先生”</p>
                <p>“这是够糟糕的，但是房子，旅馆，城堡，有什么分别，我爱你就够了。”</p>
                <p>探戈舞场内，一个盛大的探戈比赛正接近尾声，一个带着眼睛的滑稽老女人评委宣布：祝福最后的探戈！而让娜和保罗也走进了舞场，保罗对让娜说：“如果音乐是爱情的食物，那么就请继续演奏吧！”喝醉的两个人在一对对一本正经的舞者面前就像两个胡闹的小孩子，老女人急忙干涉，保罗对她宣布：“这是爱情！”</p>
                <p>“但是这是比赛，不适合爱情，去电影院看爱情吧。”</p>
                <p>保罗被拉出了舞场，但是他脱下裤子，用屁股表示抗议，这时让娜已经彻底怕了保罗，她又逃了出来，但是保罗穷追不舍，脸上始终带着玩闹的笑容，让娜越来越怕，她跑回了家，保罗竟然也追了进来，他并没有看到她手中已经握住了手枪，在保罗充满激情的示爱中，让娜扣动了扳机，保罗被打中后走向了阳台，仿佛一句话没有说完：“我们的孩子……”，面对巴黎空虚而又拥挤的城市景色，他茫然说了一句：“妈妈！”就倒地了，保罗死在了阳台上，身体蜷缩着，好似又回归了母体。</p>


                <p>我想，这部电影的一个隐含的主题是漂泊的保罗的爱，是他的飘荡的心灵无所依托下的感情与身体的经历，而导演贝托鲁奇，这个意大利人，老是拍摄拍摄那些关于异乡和异乡人的影片，我也十分迷恋他影片中的氛围，那些“在别处”的色彩和影调，包括他对那个时期美术，政治，音乐，文化背景的把握与呈现，我仿佛能穿透时空的重重迷雾，感同身受地体验那种漂泊。海明威，斯坦因，乔伊斯，毕加索，巴黎左岸，新浪潮电影，那个喧闹中的巴黎，毛玻璃透出的暖黄色灯光使人感到片刻的温暖，却又能加剧漂泊中的寒冷，在影片中，让娜曾问保罗为什么不回美国，保罗含混地说：“我想，是一些不好的记忆吧。”在保罗的讲述中，我们看到这个强壮的男人的童年，其实，那是我们能体验到童年，这与巴黎人让娜奶油蛋糕式的童年截然相反，保罗其实是个孩子，他一直处在童年爱的缺失所带来的遗憾之中，这种遗憾使他宿命般地寻找失去的爱，就像他对让娜说的：“不久以后，他就需要你帮他建立堡垒，从你的胸部，微笑，毛发和味道建立起来，一个让他觉得安全和舒适的地方。”</p>
                <p>那个堡垒，就是女人，就是爱情吧。</p>

                <p>这绝对不是贝托鲁奇自身感情的简单投射，我更不想说这是我观看到的镜中的自己……作为一部电影，《巴黎，最后的探戈》早已经成为经典，它拍得很美，精致的构图，色彩，始终回旋激荡的恰如其分的爵士音乐使它的每一个场景，镜头都成为令人回味不已的诗篇，不光是影像的诗篇，还是氛围和情感的诗篇，它有种带人离开现实，走入时间深处的魔力。</p>
            </div>
        );
    }
}

export default DYH;


